import React, {useLayoutEffect} from "react";
import {graphql, Link, useStaticQuery} from "gatsby";
import anime from "animejs";
import Img from "gatsby-image"
import {useTranslation} from "react-i18next";

export default function Projects() {
    const {t, i18n} = useTranslation();

    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "projects"}}}}}
      ) {
          nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            language {
              slug
            }
            featuredImage {
              node {
                sourceUrl
                 localFile {
                   publicURL
                   childImageSharp {
                     gatsbyImageData(
                        placeholder: BLURRED
                     )
                     fluid(maxWidth: 2000, quality: 100) {
                         ...GatsbyImageSharpFluid
                     }
                   }
                 }
              }
            }
          }
      }
    }
  `)

    useLayoutEffect(() => {
        require('../../../scss/projects.scss');
        let timeline = anime.timeline({
            easing: 'linear',
            autoplay: true,
        });
        timeline.add({
            targets: '#projects',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            scale: [0.8, 1],
            duration: 1000
        });
        timeline.add({
            targets: '.css--fadedtext',
            easing: 'easeOutExpo',
            opacity: [0, 1],
            translateX: ["-100%", 0],
            duration: 500
        });
    }, [])

    function capitalizeFirstLetter(str) {
        return str[0].toUpperCase() + str.slice(1);
    }

    return (
        <div id="projects" className="mt-20 md:mt-18 md:px-0 md:container px-4 mx-auto">
            <h1 className="text-3xl md:text-5xl glitch text-center" data-glitch={t('success_stories')}>
                {t('success_stories')}
            </h1>
            <div className="grid md:grid-cols-1 gap-4 mt-12 relative pb-8">
                {data.allWpPost.nodes.map((node, index) => (
                    node.language.slug === i18n.language &&

                    <Link to={node.slug+"/"} key={index}>

                        <div className="relative pb-8" key={index}>
                            <div className="flex md:justify-start justify-center md:items-start items-center" key={index}>
                                <div className="rounded w-full css--projects-bg md:grid">
                                    <div className={"css--projects-photo px-10 md:px-0 " + (index % 2 ? "css--projects-photo-left" : "css--projects-photo-right")}>
                                        <Img className="object-contain h-full w-full" fluid={node.featuredImage.node.localFile.childImageSharp.fluid} alt="Project image"/>
                                    </div>
                                    <div className={'flex flex-col justify-between md:pb-10 lg:pb-20 ' + (index % 2 ? "css--projects-text-right" : "css--projects-text-left")}>
                                        <div className="px-6 py-4">
                                            <div className="mb-2" dangerouslySetInnerHTML={{__html: node.excerpt}}/>
                                            <div className="font-bold text-white text-xl md:text-3xl mb-2">{node.title}</div>
                                        </div>
                                        <div className={"px-6 md:pt-4 pb-20 md:pb-2 flex " + (index % 2 ? "flex-row" : "md:flex-row-reverse")}>
                                            {node.tags.nodes.map((tag, i) => (
                                                <span className="text-center text-white text-xs mr-5" key={i}>
                                                        <img src={`/images/develop_stack/${tag.name}.svg`} style={{height: "30px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                                                    {capitalizeFirstLetter(tag.name)}
                                                    </span>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Link>

                ))}
            </div>

        </div>
    )
}
