import '../i18n/i18n';

import React from "react"
import FadedText from "../components/elements/FadedText";
import Navigation from "../components/layout/Navigation";
import Projects from "../components/layout/Portfolio/Projects";
import {SEO} from "../components/seo";

const Portfolio = () => {
    return (
        <main className="main-wrapper">
            <Navigation pageNr={'K03PR'}/>
            <FadedText text={'DISCOVER'} direction="bottom"/>

            <div className="py-14 relative min-h-screen">
                <Projects/>
            </div>
        </main>
    )
}
export default Portfolio;
export const Head = () => <SEO title={"Portfolio"} description={'Some of our projects in a past year.'}/>

